.swimply {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  gap: var(--gap-5xs);
  min-height: 940px;
  z-index: 2;
  text-align: center;
  font-size: var(--text-5xl-font-black-size);
  color: var(--white-smoke);
  font-family: var(--text-body-font-medium-full-caps);
  padding: var(--padding-5xl) 0;
}

/* ============= */
.image-mask-cover {
  width: 1614.7px;
  height: calc(100% - 6.4px);
  position: absolute;
  margin: 0 !important;
  top: 3px;
  bottom: 3.4px;
  left: -89px;
  max-height: 100%;
  z-index: 1;
}

/* ============= */
.image-mask {
  width: 1148px;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  gap: var(--gap-93xl);
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}

/* ============== */
.blob-node {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-37xl);
}

.swift-is-swimply-container {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.05em;
  line-height: 80px;
  font-weight: 900;
  font-family: inherit;
}

.sw2 {
  color: var(--coquelicot-600-main);
}

.others-just-dont {
  align-self: stretch;
  position: relative;
  font-size: var(--text-lg-font-normal-size);
  line-height: 28px;
  color: var(--night-300);
}

/* =================== */

.symbol-node {
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
  align-self: stretch;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 24px;
  font-size: var(--text-2xl-font-bold-size);
}

.symbol-node>* {
  height: 100%;
  flex: 1;
  border-radius: var(--br-base);
  background: linear-gradient(180deg,
      rgba(58, 63, 63, 0.7),
      rgba(58, 63, 63, 0));
  align-items: center;
  padding: var(--padding-5xl);
  gap: var(--gap-21xl);
  min-height: 500px;
  min-width: 270px;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.shape-text-node1 {
  box-shadow: 0 0 20px 3px rgba(242, 62, 13, 0.6),
    0 0 4px 1px rgba(242, 62, 13, 0.45);
  background: linear-gradient(180deg, #f23e0d, rgba(242, 62, 13, 0.2));
}
@media screen and (max-width: 750px) {

  .symbol-node {
    flex-direction: column;
    gap: 24px;
    padding: auto;
    align-self: stretch;
  }
  .symbol-node:nth-child(3) { order: 1; }
  .symbol-node:nth-child(1) { order: 2; }
  .symbol-node:nth-child(2) { order: 3; }
}

